import { injectable } from 'inversify';
import { IGraphGenerator } from './graph-generator';
import { SModelIndex, SModelElementSchema, SNodeSchema, SNode, SModelElement } from 'sprotty';
import { ElkNode, ElkLabel, ElkPort, ElkShape, ElkEdge } from 'elkjs/lib/elk-api';
//@ts-ignore
import * as pixelWidth from 'string-pixel-width';

@injectable()
export class GraphGenerator implements IGraphGenerator {
    readonly nodes: SNodeSchema[] = [];
    readonly index = new SModelIndex<SModelElementSchema>();

    add(node: SNodeSchema): void {
        // this.nodes.push(node);
        this.index.add(node);
    }

    update(node: SNodeSchema): void {
        this.index.remove(node);
        this.index.add(node);
    }

    clear(): void {
        const list = this.index.all();
        list.forEach(id => this.index.remove(id));
        // console.log(this.index);
    }

    generateNode(id: string, shape: ElkShape | undefined, option: { [key: string]: string }, version?: string): ElkNode {
        const labeloption: { [key: string]: any } = {};
        labeloption["nodeLabels.placement"] = "[OUTSIDE, H_CENTER, V_TOP]";
        const label = this.generateLabel(id, id, labeloption);

        const newElkNode = {
            id: id,
            width: (shape !== undefined) ? shape.width : 1,
            height: (shape !== undefined) ? shape.height : 1,
            labels: [label],
            ports: Array(),
            layoutOptions: option
        };

        return newElkNode;
    }

    generateLabel(id: string, text: string, option: { [key: string]: string }, version?: string): ElkLabel {
        // console.log(options);
        const labelWidth = pixelWidth(text, { size: 10 });
        // console.log(labelWidth);
        const label = {
            id: `${id}/label`,
            text: text,
            layoutOptions: option,
            width: labelWidth * 1.5,
            height: 13.34,
        };

        return label;
    }


    generatePort(id: string, parent: string, option: { [key: string]: string }, version?: string): ElkPort {

        const pid = `${parent}_${id}`;
        const labeloption: { [key: string]: any } = {};
        // labeloption["nodeLabels.placement"] = "[INSIDE, V_TOP, H_CENTER]";
        const label = this.generateLabel(pid, id, labeloption);

        label.height = 20;

        const port = {
            id: pid,
            width: 5,
            height: 5,
            layoutOptions: option,
            labels: [label],
        };

        // console.log(pid);

        return port;
    }

    generateEdge(id: string, srcid: string, tarid: string): ElkEdge {

        const edge = {
            id: id,
            sources: [
                srcid
            ],
            targets: [
                tarid
            ]
        };

        return edge;
    }
}