import { injectable, inject } from "inversify";
import { ElkNode, ElkPort, ElkShape, ElkLabel } from "../json/elkgraph-json";
import { IGraphGenerator } from '../graph/graph-generator';
//@ts-ignore
import * as pixelWidth from "string-pixel-width";
import { PrimitiveModules, PrimitiveNodes } from "./primitive/modules";
import { NetMap } from "./net-map";

export const NetlistManager = Symbol('NetlistManager');

export class NetProperty {
    name: string;
    output: string;
    inputs: [string];
}

export class ConstNetProperty {
    name: string;
    input_port_id: string;
    value: string;
}

@injectable()
export class NetlistToElk {

    private nodes: Map<string, ElkNode>;
    private graphs: Map<string, ElkNode>;

    @inject(IGraphGenerator)
    public readonly graphGenerator: IGraphGenerator;


    @inject(PrimitiveNodes)
    public readonly primitiveGenerator: PrimitiveModules;

    constructor() {
        console.log("constructed NetlistToElk");
        this.nodes = new Map();
        this.graphs = new Map();
    }

    // constructor(netlist: any) {

    //     this.nodes = new Map();
    //     this.graphs = new Map();
    //     this.transform(netlist);
    // }

    public clearElements() {
        this.nodes.clear();
        this.graphs.clear();
    }

    public getModule(name: string): ElkNode | undefined {
        return this.nodes.get(name);
    }

    public getGraph(name: string): ElkNode | undefined {
        return this.graphs.get(name);
    }

    public transform(netlist: any) {
        const modules = Object.entries(netlist.modules);
        const index: Map<string, Object> = new Map();
        //making node
        modules.forEach((module) => {
            const name = module[0];
            const property = module[1] as any;
            let node = this.genNewELknode(name);

            const ports = Object.entries(property.ports).map(p => {
                return this.genNewElkPort(name, p);
            });

            // console.log(ports);
            node.ports = ports;

            this.nodes.set(name, node);

            // const cells = property.cells;
            // const netnames = property.netnames;

            // index.set(name,
            //     {
            //         cells: cells,
            //         netnames: netnames
            //     });

            index.set(name, property);

            // Object.entries(children).forEach((child) => { });
        });

        index.forEach((obj, k) => {
            // console.log(k);
            const graph = this.genNewGraph(obj);
            this.graphs.set(k, graph);
        });

        // console.log(this.graphs);

    }

    protected genNewGraph(obj: any): ElkNode {
        const ports = obj.ports;
        const cells = obj.cells;
        const nets = obj.netnames;

        let graph = {
            id: "root",
            layoutOptions: { 'algorithm': 'layered' },
            children: Array(),
            edges: Array(),
        }

        const netmap = new NetMap();

        // Object.entries(nets).forEach((net) => {
        //     netmap.add(net);
        // });

        Object.entries(cells).forEach((cell) => {
            const instname = cell[0];
            const context = cell[1] as any;
            const modulename = context.type;

            let child: ElkNode;
            if (modulename.slice(0, 1) === '$') {
                child = this.genPrimitiveNode(instname, context);
                //Todo Port
            } else {
                child = this.nodes.get(modulename) as ElkNode;
                child = this.instanciate(modulename, instname, child);
            }

            // console.log(context.port_directions);
            // console.log(context.connections);

            const dirs = Object.entries(context.port_directions);
            Object.entries(context.connections).forEach((connection, index) => {
                const pname = connection[0];
                const bits = connection[1] as [];
                const dir = dirs[index][1] as string;
                // console.log(pname);
                // console.log(connection[1]);
                // console.log(dirs[index]);

                const pid = `${modulename}:${instname}_${pname}`;
                netmap.port_add(pid, bits, dir);
            });

            graph.children.push(child);
        });

        Object.entries(ports).forEach((p) => {
            const portname = p[0];
            const portproperty = p[1] as any;
            const option: { [key: string]: any } = {};
            option["spacing.portPort"] = 20;
            option["nodeSize.minimum"] = "(30.0, 13.340)";
            // option["nodeSize.maximum"] = "(20.0, 10.0)";
            option["nodeSize.constraints"] = "[MINIMUM_SIZE]";
            option["portConstraints"] = "FIXED_SIDE";
            // const shape: ElkShape = {
            //     width: 10,
            //     height: 5,
            // };

            const portnode = this.graphGenerator.generateNode(portname, undefined, option);

            netmap.export_add(portname, portproperty.bits, portproperty.direction);

            graph.children.push(portnode);
        });

        Object.entries(nets).forEach((netobj: any) => {
            const name = netobj[0];
            const bits = netobj[1].bits;
            const hidden = netobj[1].hidden;

            netmap.net_add(name, bits, hidden);

        });

        netmap.net_bundle();

        // console.log(netmap.port_const);
        // console.log(netmap.net_map);

        netmap.net_connection.forEach((value, key) => {
            // console.log(key);
            const edge = this.graphGenerator.generateEdge(key, value.srcid, value.tarid);
            graph.edges.push(edge);
        });

        netmap.const_connection.forEach((value) => {
            const lsb = value.lsb;
            const msb = value.msb;
            const constnodeid = `${value.id}[${msb}:${lsb}]`;

            const labeloption: { [key: string]: any } = {};
            // labeloption["nodeLabels.placement"] = "[OUTSIDE, H_CENTER, V_TOP]";
            labeloption["nodeLabels.placement"] = "[INSIDE]";
            const label = this.graphGenerator.generateLabel(constnodeid, value.value, labeloption);
            label.y = -2.5;
            label.x = 0.4;

            const option: { [key: string]: any } = {};
            option["nodeSize.minimum"] = `(${label.width}, ${label.height})`;
            option["nodeSize.constraints"] = "[MINIMUM_SIZE]";
            option["portConstraints"] = "FIXED_SIDE";
            const constnode = this.graphGenerator.generateNode(constnodeid, undefined, option);

            (constnode.labels as ElkLabel[])[0] = label;

            const constedgeid = `${constnodeid}>${value.id}`;
            const constedge = this.graphGenerator.generateEdge(constedgeid, constnodeid, value.id);
            graph.children.push(constnode);
            graph.edges.push(constedge);
        });

        // console.log(netmap.const_connection);

        // netmap.port_const.forEach((value, key) => {
        //     console.log(key);
        //     const lsb = value.bits[0];
        //     const msb = value.bits[value.bits.length - 1];
        //     const constnodeid = `${key}[${msb}:${lsb}]`;

        //     const labeltext = value.values.reverse().join("");
        //     const labeloption: { [key: string]: any } = {};
        //     labeloption["nodeLabels.placement"] = "[OUTSIDE, H_CENTER, V_TOP]";
        //     const label = this.graphGenerator.generateLabel(constnodeid, labeltext, labeloption);

        //     const option: { [key: string]: any } = {};

        //     option["nodeSize.minimum"] = `(${label.width}, ${label.height})`;
        //     option["nodeSize.constraints"] = "[MINIMUM_SIZE]";
        //     option["portConstraints"] = "FIXED_SIDE";
        //     const constnode = this.graphGenerator.generateNode(constnodeid, undefined, option);

        //     (constnode.labels as ElkLabel[])[0] = label;

        //     const constedgeid = `${constnodeid}>${key}`;
        //     const constedge = this.graphGenerator.generateEdge(constedgeid, constnodeid, key);
        //     graph.children.push(constnode);
        //     graph.edges.push(constedge);
        // });

        // netindex.forEach((net) => {
        //     const output = net.output;
        //     net.inputs.forEach((input) => {
        //         if (output !== "" && input !== "") {

        //             const eid = `${output}>${input}`;
        //             const edge = this.graphGenerator.generateEdge(eid, output, input);
        //             console.log(edge);
        //             graph.edges.push(edge);
        //         }
        //     });
        // });

        return graph;
    }

    protected instanciate(name: string, instname: string, node: ElkNode): ElkNode {
        const newname = `${name}:${instname}`;
        const regex = new RegExp(name);

        const newNode = JSON.parse(JSON.stringify(node)) as ElkNode;

        // console.log(name);
        // console.log(newname);
        newNode.id = node.id.replace(regex, newname);
        //adjust Size

        newNode.labels?.forEach((label) => {
            const newtext = label.text.replace(regex, newname);

            label.id = label.id.replace(regex, newname);
            label.text = newtext;
            label.width = pixelWidth(newtext, { size: 12 })
        });

        newNode.ports?.forEach((port) => {
            port.id = port.id.replace(regex, newname);

            port.labels?.forEach((label) => {
                label.id = label.id.replace(regex, newname);
            });
        });

        // console.log(newnode);
        return newNode;
    }

    protected genPrimitiveNode(name: string, obj: any): ElkNode {
        return this.primitiveGenerator.generate(name, obj);
        // const node = this.primitiveGenerator.add(name);
        // return this.genNewELknode(name);
    }

    protected genNewElkPort(nodename: string, port: any): ElkPort {
        const portname = port[0];
        const portproperty = port[1];

        const option: { [key: string]: any } = {};
        option["port.side"] = (portproperty.direction === "input") ? "WEST" : "EAST";

        const p = this.graphGenerator.generatePort(portname, nodename, option);
        return p;
    }

    protected genNewELknode(name: string): ElkNode {

        const option: { [key: string]: any } = {};
        option["spacing.portPort"] = 20;
        option["nodeSize.minimum"] = "(80.0, 40.0)";
        option["nodeSize.constraints"] = "[PORTS MINIMUM_SIZE]";
        option["portConstraints"] = "FIXED_SIDE";
        option["portLabels.placement"] = "INSIDE";
        const node = this.graphGenerator.generateNode(name, undefined, option);

        return node;
    }
}