import { injectable, inject, postConstruct } from 'inversify';
import {
    LocalModelSource, SelectAction, Action,
    ActionHandlerRegistry, SelectAllAction, FitToScreenAction, SModelElementSchema, SModelElement, SGraphSchema, SGraph, SLabel, SEdgeSchema, BringToFrontAction, OpenMouseListener, OpenAction, UndoRedoKeyListener
} from 'sprotty';
import { IGraphGenerator } from './graph-generator';
import ELK, { ElkNode as ElkGraph, ElkShape } from 'elkjs/lib/elk-api.js';
import { ElkGraphJsonToSprotty } from '../json/elkgraph-to-sprotty';
import { NetlistToElk, NetlistManager } from '../netlist/netlist-manager';

@injectable()
export class GraphModelSource extends LocalModelSource {

    protected elk: any;
    public elkGraph: ElkGraph;
    protected sGraph: SGraphSchema;

    loadIndicator: (loadStatus: boolean) => void = () => { };

    @inject(IGraphGenerator)
    public readonly graphGenerator: IGraphGenerator;

    @inject(NetlistManager)
    public readonly elkelement: NetlistToElk;

    @postConstruct()
    protected postConstruct(): void {
        console.log("initialize");

        this.elkGraph = {
            id: "root",
            layoutOptions: { 'algorithm': 'layered' },
            children: [],
            edges: [],
        };

        this.elk = new ELK({
            workerUrl: '/elk/elk-worker.min.js'
        });
    }

    protected updateSprottyModel = (graph: any): Promise<SGraphSchema> => {
        // console.log(graph);
        return Promise.resolve(new ElkGraphJsonToSprotty().transform(graph));
    };

    async updateElkGraph(graph: any): Promise<void> {
        this.elkGraph = graph;
        this.graphGenerator.clear();
        let sgraph = await this.layoutModel();
        const targets = sgraph.children.filter(c => c.type === "node").map(n => {
            this.graphGenerator.add(n);
            return n.id;
        });

        const edges = sgraph.children.filter(c => c.type === `edge`).forEach((edge) => {
            this.graphGenerator.add(edge);
        });

        this.center(Array.from(targets));
    }

    layoutNetlist(netlist: any, module: string): Promise<void> {
        // this.ElkElements = new NetlistToElk(netlist);
        this.elkelement.clearElements();
        this.elkelement.transform(netlist);
        let graph = this.elkelement.getGraph(module);

        if (graph === undefined) {
            console.log(`Module ${module} is not found in Netlist`);
            graph = this.elkGraph;
        }

        graph.id = module;
        // console.log(graph);
        return this.updateElkGraph(graph);
    }

    layoutModel(): Promise<SGraphSchema> {
        // console.log(this.elkGraph);
        const sGraph = this.elk.layout(this.elkGraph)
            .then(this.updateSprottyModel)
            .then((sGraph: any) => {
                this.currentRoot = sGraph;
            })
            .then(() => {
                this.updateModel();
                return this.currentRoot;
            });

        return Promise.resolve(sGraph);
    }

    initialize(registry: ActionHandlerRegistry): void {
        console.log("initialize");
        super.initialize(registry);

        registry.register(SelectAction.KIND, this);
        registry.register(SelectAllAction.KIND, this);
        registry.register(BringToFrontAction.KIND, this);
        registry.register(OpenAction.KIND, this);
    }

    center(elementIds: string[]): Promise<void> {
        if (elementIds.length > 0) {
            return this.actionDispatcher.dispatch(<FitToScreenAction>{
                kind: 'fit',
                elementIds: elementIds.filter(id => {
                    const element = this.graphGenerator.index.getById(id);
                    return element?.type === 'node';
                }),
                padding: 20,
                maxZoom: 1,
                animate: true
            });
        } else {
            return Promise.resolve();
        }
    }

    async createEdge(srcid: string, tarid: string, version?: string): Promise<void> {

        // console.log(srcid);
        // console.log(tarid);
        const src = srcid.split(",");
        const tar = tarid.split(",");

        const snode = this.graphGenerator.index.getById(src[0]);
        const tnode = this.graphGenerator.index.getById(tar[0]);

        if (snode === undefined || tnode === undefined) {
            console.log(((snode === undefined) ? src[0] : tar[0]) + " doesn't exist.");
            return Promise.resolve();
        }

        let targetid = tar[0];
        let sourceid = src[0];

        if (src.length >= 2) {
            const sport = snode.children?.findIndex(c => c.id === src[1]) as number;
            if (sport >= 0) { //exist
                sourceid = src[1];
            }
        }

        if (tar.length >= 2) {
            const tport = tnode.children?.findIndex(c => c.id === tar[1]) as number;
            if (tport >= 0) { //exist
                targetid = tar[1];
            }
        }

        const eid = `${sourceid}>${targetid}`;
        console.log(eid);

        const isNew = this.graphGenerator.index.getById(eid) === undefined;

        if (!isNew) {
            console.log(`${eid} exist.`);
            return Promise.resolve();
        }

        const edge = this.graphGenerator.generateEdge(eid, sourceid, targetid);

        this.elkGraph.edges?.push(edge);
        // console.log(this.elkGraph);
        const sGraph = await this.layoutModel();
        // console.log(sGraph);

        let newSEdge = sGraph.children.find(c => c.id === eid) as SModelElementSchema;
        // console.log(newSEdge);
        this.graphGenerator.add(newSEdge);
        this.center([src[0], tar[0]]);
    }

    async appendPort(nodename: string, portname: string, dir: string, version?: string): Promise<void> {
        const node = this.graphGenerator.index.getById(nodename);

        if (node === undefined) {
            console.log(`${nodename} doesn't exist.`);
            return Promise.resolve();
        };

        const pid = `${nodename}_${portname}`;
        // console.log(node.children?.forEach(c => console.log(c.id)));
        const isNewPort = node.children?.findIndex(c => c.id === pid) as number;
        // console.log(isNewPort);
        if (isNewPort >= 0) {
            console.log(`${pid} already exist.`);
            return Promise.resolve();
        }

        const option: { [key: string]: any } = {};
        option["port.side"] = dir;

        const port = this.graphGenerator.generatePort(portname, nodename, option);

        let elkNode = this.elkGraph.children?.find(c => c.id === nodename);
        elkNode?.ports?.push(port);
        // console.log(elkNode?.ports);

        const sGraph = await this.layoutModel();
        let newSGraphNode = sGraph.children?.find(c => c.id === nodename) as SModelElementSchema; //referene?

        // console.log(newSGraphNode);
        this.graphGenerator.update(newSGraphNode);
        // console.log(this.graphGenerator.index.getById(nodename));

        // console.log(sGraph.children);
        this.center([nodename]);

    }

    async createNode(name: string, version?: string): Promise<void> {

        const isNew = this.graphGenerator.index.getById(name) === undefined;

        if (!isNew) {
            console.log(`${name} is already exist.`);
            return Promise.resolve();
        }

        const option: { [key: string]: any } = {};
        option["spacing.portPort"] = 25;
        option["nodeSize.minimum"] = "(80.0, 40.0)";
        option["nodeSize.constraints"] = "[PORTS MINIMUM_SIZE]";
        option["portConstraints"] = "FIXED_SIDE";
        // option["nodeLabels.placement"] = "[OUTSIDE, V_TOP, H_CENTER]";
        // portAlignment.default: CENTER

        const newElkNode = this.graphGenerator.generateNode(name, undefined, option);

        this.elkGraph.children?.push(newElkNode);

        console.log(this.elkGraph);

        const sGraph = await this.layoutModel();
        // console.log(sGraph);
        let newSGraphNode = sGraph.children?.find(c => c.id === name) as SModelElementSchema; //referene?
        // console.log(newSGraphNode);
        this.graphGenerator.add(newSGraphNode);
        // console.log(this.graphGenerator.index);

        //focus on nodes
        const gen = this.graphGenerator;
        // const target = gen.nodes.map(n => n.id);
        const target = gen.index.all().filter(e => e.type === `node`).map(n => n.id);
        // console.log(Array.from(target));
        this.center(Array.from(target));
        // this.center([name]);
    }

    handle(action: Action): void {
        switch (action.kind) {
            case SelectAction.KIND:
                //     console.log("SelectAction.KIND");
                this.handleSelect(action as SelectAction);
                break;
            case OpenAction.KIND:
                this.handleOpen(action);
                break;
            default:
                console.log(action.kind);
                super.handle(action);
        }
    }

    handleOpen(action: any) {
        // console.log(action);
        const parentid = this.elkGraph.id;
        const module = action.elementId;
        if (module.slice(0, 1) === "$") {
            console.log(`${module} is Primitive.`);
            return;
        }

        const modulename = module.split(":")[0];
        const graph = this.elkelement.getGraph(modulename);

        if (graph === undefined) {
            console.log(`${modulename} is not found in Netlist`);
            return;
        }

        const newgraph = JSON.parse(JSON.stringify(graph));
        newgraph.id = `${parentid}/${modulename}`;
        // console.log(graph);
        this.updateElkGraph(newgraph);
    }

    handleSelect(action: any) {

        const sel = action.selectedElementsIDs;
        const desel = action.deselectedElementsIDs;

        console.log(sel);
        // console.log(desel);
    }

    goUp() {
        const id = this.elkGraph.id;
        const ids = id.split("/");
        if (ids.length === 1) {
            console.log("this is Top module.");
            return;
        }

        console.log(ids.pop());
        const parentid = ids[ids.length - 1];
        const graph = this.elkelement.getGraph(parentid);

        if (graph === undefined) {
            console.log(`${parentid} is not found in Netlist`);
            return;
        }

        const newgraph = JSON.parse(JSON.stringify(graph));
        newgraph.id = ids.join("/");
        console.log(newgraph.id);
        this.updateElkGraph(newgraph);

    }
}

function getRandomInt(max: number) {
    return Math.floor(Math.random() * Math.floor(max));
}