import 'reflect-metadata';
import 'bootstrap';
import 'devbridge-autocomplete';
import * as jQuery from 'jquery';
import { TYPES } from 'sprotty';
import fontawesome from '@fortawesome/fontawesome';
import * as faBars from '@fortawesome/fontawesome-free-solid/faBars';
import { containerFactory } from './graph'
import { GraphModelSource } from './graph/model-source';
import { NetlistManager, NetlistToElk } from './netlist/netlist-manager';
import { PrimitiveNodes, PrimitiveModules } from './netlist/primitive/modules';

import 'ace-builds/src-noconflict/ace';
import 'ace-builds/src-min-noconflict/theme-monokai';
import 'ace-builds/src-min-noconflict/mode-verilog';
import 'ace-builds/src-min-noconflict/mode-json5';
// @ts-ignore //externals
import * as YosysJS from 'yosysjs';

fontawesome.library.add(faBars);

const exs: { [key: string]: string } = {
    simple: require('./example/simple.v'),
    simple2: require('./example/simple2.v'),
    submodule: require('./example/submodule.v'),
    submodule2: require('./example/submodule2.v'),
    assign: require('./example/assign.v'),
    statemachine: require('./example/statemachine.v'),
    test: require('./example/test.v'),
};

jQuery(() => {

    const container = containerFactory((bind, unbind, isBound, rebind) => {
        bind(TYPES.ModelSource).to(GraphModelSource).inSingletonScope();
        bind(NetlistManager).to(NetlistToElk);
        bind(PrimitiveNodes).to(PrimitiveModules);
    });

    const modelsource = container.get<GraphModelSource>(TYPES.ModelSource);

    const loading = jQuery('#loading').get(0) as HTMLElement;

    const packageInput = jQuery('#package-input');
    packageInput.focus();
    packageInput.keyup(event => {
        if (event.keyCode === 13) {
            let name = packageInput.val() as string;
            if (name !== "") {
                modelsource.createNode(name);
            }
            packageInput.focus();
        }
    });

    const nodeportInput = jQuery("#node-port-input");
    nodeportInput.keyup(event => {
        if (event.keyCode === 13) {
            let str = nodeportInput.val() as string;
            let names = str.split("/");

            if (names.length < 3) {
                return;
            } else {
                modelsource.appendPort(names[0], names[1], names[2]);
            }
        }
    });

    const wireInput = jQuery("#wire-input");
    wireInput.keyup(event => {
        if (event.keyCode === 13) {
            let str = wireInput.val() as string;
            let point = str.split(">");

            if (point.length < 2) {
                return;
            } else {
                modelsource.createEdge(point[0], point[1]);
            }
        }
    });

    // jQuery('body').toggleClass('sidebar-minimized');
    jQuery('#sidebar-toggle-button').click(() => jQuery('body').toggleClass('sidebar-minimized'));

    //generate graph
    async function generateGraph() {
        await modelsource.createNode("hoge");
        await modelsource.createNode("foo");
        await modelsource.appendPort("hoge", "p1", "WEST");
        await modelsource.appendPort("hoge", "p2", "WEST");
        // await modelsource.appendPort("hoge", "p3", "EAST");
        await modelsource.appendPort("foo", "p1", "WEST");
        await modelsource.appendPort("foo", "p2", "EAST");
    }

    // generateGraph();

    const goParent = jQuery("#goup");
    goParent.click(() => {
        modelsource.goUp();
    });

    const clearall = jQuery("#clearall");
    clearall.click(() => {
        // console.log(modelsource.graphGenerator.index.getById());
    });

    //@ts-ignore
    const editor = ace.edit("editor");
    //@ts-ignore
    const editor2 = ace.edit("editor2");
    //@ts-ignore
    const aceRange = ace.require('ace/range').Range;
    editor.setTheme("ace/theme/monokai");
    editor.session.setMode("ace/mode/verilog");
    editor2.setTheme("ace/theme/monokai");
    // editor2.getSession().setMode("ace/mode/verilog");
    editor2.session.setMode("ace/mode/json5");
    // editor.setValue(exs["simple"], -1);
    editor.setValue((exs["simple"] as any).default, -1);

    const exampleSelector = jQuery("#exampleSelector");

    Object.entries(exs).forEach((obj) => {
        // console.log(obj[0]);
        exampleSelector.append(`<option value="${obj[0]}">${obj[0]}</option>`);
    });

    exampleSelector.change(() => {
        const sel = exampleSelector.val() as string;
        const code = (exs[sel] as any).default;
        editor.setValue(code, -1)
    });

    const yscommand = [
        "design -reset;",
        "read_verilog input.v;",
        "hierarchy -auto-top;",
        "proc;",
        "opt_clean;",
        "write_json netlist.json;",
        "clean;"
    ].join(" ");

    let eflag = 0;
    const err_handle = (logmsg: string, errmsg: string): void => {
        console.log(logmsg);
        console.log(errmsg);
        if (errmsg !== "") {
            eflag = 1;
        }
    };

    let ys = YosysJS.create_worker(function () { console.log("ready") });
    ys.verbose(true);

    const diagram = jQuery("#diagram");
    diagram.click(() => {

        if (ys !== undefined) {
            editor2.setValue("", 1);
            eflag = 0;
            const code = editor.getValue();
            ys.write_file("input.v", code);
            ys.run(yscommand, err_handle);
            ys.read_file("netlist.json", (function (text: string) {

                if (eflag !== 1) {
                    editor2.setValue(text, 1);
                    modelsource.layoutNetlist(JSON.parse(text), "Top");
                } else {
                    console.log("yosys error");
                }
            }));
        } else {
            console.log("ys is undefined.");
        }
    });

    const showelk = jQuery("#showelk");
    showelk.click(() => {
        console.log(modelsource.elkGraph);
    });
});

