export default "module Top(input clk, input reset, input A, input B, output[1:0] state_out);\n    wire clk, reset, A, B;\n    wire[1:0] state_out;\n\n    assign state_out = state;\n\n    parameter S0 = 2'b00;\n    parameter S1 = 2'b01;\n    parameter S2 = 2'b10;\n    parameter S3 = 2'b11;\n\n    reg[1:0] state = S0;\n\n    always@(posedge clk)begin\n        if(reset == 1'b1)begin\n            state <= S0;\n        end\n        else begin\n            case(state)\n                S0: begin\n                    if(A == 1'b1)begin\n                        state <= S1;\n                    end\n                end\n                S1: begin\n                    if(B == 1'b1)begin\n                        state <= S1;\n                    end\n                end\n                S2: begin\n                    if(A == 1'b0)begin\n                        state <= S2;\n                    end\n                end\n                S3: begin\n                    if(B == 1'b0)begin\n                        state <= S0;\n                    end\n                end\n                default: begin\n                    state <= S0;\n                end\n            endcase\n        end\n    end\nendmodule";