import { ElkNode, ElkShape } from '../../json/elkgraph-json';
import { injectable, inject } from 'inversify';
import { IGraphGenerator } from '../../graph/graph-generator';
import { ElkLabel } from 'elkjs/lib/elk-api';

export const PrimitiveNodes = Symbol("PrimitiveNodes");

@injectable()
export class PrimitiveModules {

    @inject(IGraphGenerator)
    public readonly graphGenerator: IGraphGenerator;

    generate(name: string, obj: any): ElkNode {
        const primitiveName = name.split('$')[2];
        let node;

        node = this.add(name, obj);
        switch (primitiveName) {
            case "add":
                // node = this.add(name, obj);
                break;
            case "procdff":
                break;
            default:
                break;
        }

        return node;
    }

    add(name: string, obj: any): ElkNode {
        let nodeoptions = {} as any;
        nodeoptions["spacing.portPort"] = 20;
        nodeoptions["nodeSize.minimum"] = "(40.0, 40.0)";
        // nodeoptions["nodeSize.maximum"] = "(40.0, 40.0)";
        nodeoptions["nodeSize.constraints"] = "[PORTS MINIMUM_SIZE]";
        nodeoptions["portConstraints"] = "FIXED_SIDE";
        nodeoptions["portLabels.placement"] = "INSIDE";


        const ports = obj.port_directions;
        const modulename = obj.type;
        const labeltext = modulename.split('$')[1];
        const labeloption: { [key: string]: any } = {};
        let node = this.graphGenerator.generateNode(name, undefined, nodeoptions);
        labeloption["nodeLabels.placement"] = "[OUTSIDE, H_CENTER, V_TOP]";
        let label = this.graphGenerator.generateLabel(name, labeltext, labeloption);
        (node.labels as ElkLabel[])[0] = label;

        const parray = Object.entries(ports).map((obj, index) => {
            const portname = obj[0];
            const dir = obj[1];

            const option: { [key: string]: any } = {};
            option["port.side"] = (dir === "input") ? "WEST" : "EAST";

            return this.graphGenerator.generatePort(portname, `${modulename}:${name}`, option);
        });

        node.ports = parray;

        return node;
    }
};