import { SModelIndex, SModelElementSchema, SNodeSchema } from 'sprotty';
import { ElkNode, ElkLabel, ElkPort, ElkShape, ElkEdge } from 'elkjs/lib/elk-api';

export interface IGraphGenerator {
    readonly nodes: SNodeSchema[];
    // readonly edges: GraphEdgeSchema[]
    readonly index: SModelIndex<SModelElementSchema>;

    add(node: SNodeSchema): void;
    update(node: SNodeSchema): void;
    clear(): void;

    generateNode(id: string, shape: ElkShape | undefined, option: { [key: string]: string }, version?: string): ElkNode;
    generateLabel(id: string, text: string, option: { [key: string]: string }, version?: string): ElkLabel;
    generatePort(id: string, parent: string, option: { [key: string]: string }, version?: string): ElkPort;
    generateEdge(id: string, srcid: string, tarid: string): ElkEdge;
}

export const IGraphGenerator = Symbol('IGraphGenerator');