export default "module Top(input clk, output[3:0] a, output[3:0] b);\n    wire clk;\n    wire[3:0] a, b;\n\n    Sub s1(clk, a);\n    Sub s2(clk, b);\nendmodule\n\nmodule Sub(input clk, output[3:0] a);\n    wire clk;\n    reg[3:0] a;\n\n    always@(posedge clk)begin\n        a <= a + 3'b0001;\n    end\nendmodule";