

export class NetProperty {
    net_name: string[]; //net_name
    hidden: number;
    from: string; //port_id
    to: string[]; //port_ids
}

export class ConstBits {
    bits: number[];
    values: string[];
}

export class ConstConnection {
    id: string;
    msb: number;
    lsb: number;
    value: string;
}

export class NetBits {
    label: string;
    srcid: string;
    tarid: string;
    bits: number[];
}

export class NetMap {

    public net_map: Map<number, NetProperty>;
    public port_const: Map<string, ConstBits>;
    public net_const: Map<string, ConstBits>;

    public const_connection: ConstConnection[];
    public net_connection: Map<string, NetBits>;

    constructor() {
        this.net_map = new Map();
        this.port_const = new Map();
        this.net_const = new Map();
        this.const_connection = new Array();
        this.net_connection = new Map();
    }

    net_bundle() {
        this.port_const.forEach((constbits, key) => {
            console.log(key);
            console.log(constbits);
            const len = constbits.bits.length;
            let diffs = Array();

            // console.log(diffs);
            let hoge: ConstConnection[] = Array();

            let con = {
                id: key,
                msb: 0,
                lsb: 0,
                value: constbits.values[0],
            };

            if (len === 1) {
                diffs = [];
                hoge.push(con);
            } else {
                diffs = constbits.bits.slice(0, len - 1).map((bit, index) => {
                    return constbits.bits[index + 1] - bit;
                });

                diffs.forEach((diff, index) => {
                    if (diff !== 1) {
                        con.msb = constbits.bits[index];
                        hoge.push(con);
                        con = {
                            id: key,
                            msb: constbits.bits[index + 1],
                            lsb: constbits.bits[index + 1],
                            value: constbits.values[index + 1],
                        };
                    } else {
                        con.value = constbits.values[index + 1] + con.value;
                    }

                    if ((index + 1) === diffs.length) {
                        con.msb = constbits.bits[index + 1];
                        hoge.push(con);
                    }
                });
            }

            this.const_connection = this.const_connection.concat(hoge);
        });

        console.log(this.const_connection);

        //??
        this.net_const.forEach((value, key) => {
            // console.log(key);
            // console.log(value);
        });

        this.net_map.forEach((value, key) => {
            // console.log(key);
            // console.log(value);

            const tos = value.to;

            tos.forEach((to) => {
                const id = `${value.from}>${to}`;

                if (!this.net_connection.has(id)) {
                    const tmp: NetBits = {
                        label: value.net_name[0],
                        srcid: value.from,
                        tarid: to,
                        bits: [key],
                    };

                    this.net_connection.set(id, tmp);
                } else {
                    let tmp = this.net_connection.get(id) as NetBits;
                    tmp.bits.push(key);
                }
            });
        });

        // console.log(this.net_connection);
    }

    net_add(name: string, connections: [], hidden: number) {
        connections.forEach((net_num, index) => {
            if (typeof net_num === "string") {
                if (this.net_const.has(name)) {
                    let tmp = this.net_const.get(name) as ConstBits;

                    tmp.bits.push(index);
                    tmp.values.push(net_num);
                } else {
                    const tmp: ConstBits = {
                        bits: [index],
                        values: [net_num],
                    };

                    this.net_const.set(name, tmp);
                }
            } else {
                if (this.net_map.has(net_num)) {
                    let tmp = this.net_map.get(net_num) as NetProperty;
                    tmp.net_name.push(name);
                    tmp.hidden = hidden;
                }
            }
        });
    }

    export_add(id: string, connections: [], dir: string) {
        const flip_dir = (dir === "input") ? "output" : "input";
        this.port_add(id, connections, flip_dir);
    }

    port_add(id: string, connections: [], dir: string) {
        connections.forEach((net_num, index) => {
            if (typeof net_num === "string") {
                //const value
                if (this.port_const.has(id)) {
                    let tmp = this.port_const.get(id) as ConstBits;

                    tmp.bits.push(index);
                    tmp.values.push(net_num)

                } else {
                    let tmp: ConstBits = {
                        bits: [index],
                        values: [net_num],
                    };

                    this.port_const.set(id, tmp);
                }
            } else {
                if (this.net_map.has(net_num)) {
                    let tmp = this.net_map.get(net_num) as NetProperty;
                    if (dir === "output") {
                        tmp.from = id;
                    } else {
                        tmp.to.push(id);
                    }
                } else {
                    let tmp: NetProperty = {
                        net_name: [],
                        hidden: 0,
                        from: "",
                        to: [],
                    };

                    if (dir === "output") {
                        tmp.from = id;
                    } else {
                        tmp.to.push(id);
                    }

                    this.net_map.set(net_num, tmp);
                }
            }
        });
    }

}